
.adslider {
  display: flex;
  position: relative;
  &_horizontal {
    width: 100%;
    height: 8rem;
  }
  &_vertical {
    width: 50%; 
    height: 100%;
  }
  &__track {
    background-color: #e1e1e1;
    border-radius: 0.375rem;
    position: absolute;
    cursor: pointer;
    &_horizontal {
      width: 100%;
      height: 0.5rem;
      bottom: 2rem;
    }
    &_vertical {
      width: 0.5rem;
      height: 100%;
      left: 2rem;
    }
  }
  &__note {
    min-width: 2rem;
    height: 2.1875rem;
    padding: 0 0.25rem;
    background-color: #e34d2f;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 0.25rem;
    box-sizing: border-box;
    &::after {
      content: "";
      position: absolute;
      border: 0.4375rem solid transparent;
      border-top: 0.375rem solid #e34d2f;
    }
    &_horizontal {
      bottom: 4rem;
      transform: translate(-50%, 0);
      &::after {
        top: 2.125rem;
      }
    }
    &_vertical {
      left: 4.3rem;
      transform: translate(0, 50%);
      &::after {
        left: -0.83rem;
        transform: rotate(90deg);
      }
  
    }
    &_show {
      display: flex;
      visibility: visible;
    }
    &_hide {
      visibility: hidden;
      // display: none;
    }
  }
  &__value {
    font-size: 1.25rem;
    color: white;
    user-select: none;
    width: max-content;
  }
  &__handler {
    width: 1.875rem;
    height: 1.875rem;
    border-radius: 50%;
    background-color: #e34d2f;
    position: absolute;
    cursor: pointer;
    z-index: 1;
    &_horizontal {
      top: -0.6875rem;
    }
    &_vertical {
      left: -0.6875rem;
    }
  }
  &__bar {
    background-color: #e34d2f;
    border-radius: 0.375rem;
    position: absolute;
    bottom: 0;
    cursor: pointer;
    &_horizontal {
      width: 100%;
      height: 0.5rem;
      bottom: 2rem;
    }
    &_vertical {
      width: 0.5rem;
      height: 100%;
      left: 2rem;
    }
  }
  &__scale {
    display: flex;
    position: absolute;
    justify-content: space-between;
    cursor: pointer;
    &_horizontal {
      bottom: -15px;
      padding-top: 15px;
      padding-bottom: 20px;  
    }
    &_vertical {
      left: -15px;
      padding-left: 20px;
      padding-right: 15px;
      flex-direction: column-reverse;
    }
  
  }
  &__scaleLine {
    background-color: black;
    position: relative;
    &_horizontal {
      width: 2px;
      height: 12px;
    }
    &_vertical {
      height: 2px;
      width: 12px;
    }
  }
  &__scaleText {
    position: absolute;
    &_horizontal {
      top: 16px;
      transform: translate(-50%, 0);
    }
    &_vertical {
      transform: translate(0, -50%);
      right: 15px;
    }
  }
}

